import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

class FreeBoiler extends React.Component {
  render() {
    return (
      <div>
        <p class="occupied">Someone is using boiler</p>
        <button onClick={() => this.props.onClick("Free")}>Free it</button>
      </div>
    );
  }
}

class OccupiyBoiler extends React.Component {
  render() {
    return (
      <div>
        <p  class="free">No one using boiler now</p>
        <button onClick={() => this.props.onClick("Occupied")}>Occupy it</button>
      </div>
    );
  }
}

class StateChanger extends React.Component {
  render() {
    if (this.props.state.match("Free")) {
      return (
        <OccupiyBoiler onClick={this.props.onClick}/>
      );
    } else {
      return (
        <FreeBoiler onClick={this.props.onClick}/>
      );
    }
  }
}

class State extends React.Component {
  render() {
    return (
    <p>boiler is {this.props.state}</p>
    );
  }
}

class Boiler extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      state: "Free"
    };
    this.queryState();
  }

  queryState = () => {
    fetch('/state')
    .then(function(response) {
      return response.json();
    })
    .then(function(myJson) {
      alert(myJson);
    });
  }

  postStateChange = (newState) => {
    let url = "";
    if (newState.match("Free")) {
      url = "state/free";
    } else {
      url = "state/occupied";
    }
    fetch(url, {method: 'POST'})
    .then(function(response) {
      return response.json();
    })
    .then(function(myJson) {
      alert(myJson);
    });
  }

  handleStateChange = (newState) => {
    this.setState({state: newState});
  }

  render() {
    return (
      <div className="boiler">
        <div className="boiler-state">
          <State state={this.state.state}/>
        </div>
        <div className="State-Changer">
          <StateChanger onClick={this.handleStateChange} state={this.state.state} />
        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(
  <Boiler />,
  document.getElementById('root')
);
